@use "base/variables" as *;
@use "base/mixins" as *;

.admin-menu {
    flex-wrap: wrap;
    justify-content: space-around !important;
    width: 100%;
}

.no-style {
    border: none;
    text-transform: capitalize;
    background-color: transparent;
    color: $blanco;
    padding: 1rem;
    transition: all .3s;

    &:hover {
        background-color: $blanco;
        color: $negro;
        transition: all .3s;

    }
}

.busqueda {
    form {
        padding: 0 0 2rem 0 !important;
    }
}

.reservas-tamaño-completo {
    max-width: 100% !important;
    width: 100%;
}

.formulario {
    display: flex;
    flex-direction: column;

    @include Pantalla ($telefono) {
        flex-direction: row !important;
        gap: 2rem;

        div {
            flex-basis: calc(50% - 1rem);
        }
    }
}

h3 {
    text-align: start;

    .delgado {
        font-weight: $regular;
        display: block;
    }
}

.small {
    font-size: 1.8rem;
    margin: 0;
}

/* From uiverse.io */
.icono_grande{
    background: #00BD52 !important;
    & svg {
        width: 2.5rem !important;
        fill: #eee;
    }

    & .icon {
        border-left: 1px solid #007E36 !important;
    }

    &:hover {
        & .icon {
            width: 16rem;
            border-left: none !important;
        }
    }
}

.noselect {
    width: 15rem;
    height: 4rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    border: none;
    // border-radius: 5px;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.15);
    background: #e62222;
    margin: 2rem auto 1rem auto;

    & span {
        transition: 200ms;
    }

    & .text {
        color: $blanco;
    }

    & .icon {
        position: absolute;
        border-left: 1px solid #c41b1b;
        transform: translateX(110px);
        height: 4rem;
        width: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    & svg {
        width: 1.5rem;
        fill: #eee;
    }

    &:hover {
        background: #ff3636;

        & .text {
            color: transparent;
        }

        & .icon {
            width: 15rem;
            border-left: none;
            transform: translateX(0);
        }
    }

    &:focus {
        outline: none;
    }

    &:active .icon svg {
        transform: scale(0.8);
    }
}

//Dashboard
.contenedor-admin {
    background-color: $negro;

    .header-admin {
        justify-content: space-between;
        padding: 2rem 3rem;
        width: 100%;
        align-items: center;

        .negro {
            color: $blanco;
            margin: 0;
        }

        @include Pantallas (320px) {
            flex-direction: column;
        }
    }

    .header-admin,
    .navegacion-aside {
        display: flex;
        align-content: center;
        background-color: #191C24;
    }

    .contenedores {
        display: grid;
        grid-template-columns: 20% 80%;

        @include Pantallas (1000px) {
            grid-template-columns: none;
        }

        .navegacion-section {
            margin: 2rem;
            border-radius: .1rem;
            display: flex;
            flex-direction: column;
            gap: 2rem;
        }

        .seccion-informacion {
            display: flex;
            gap: 1rem;
            width: 100%;
            overflow-wrap: anywhere;
        }

        .card-body {
            background-color: #191C24;
            width: 100%;
            border-radius: 0.25rem;

            h2 {
                margin-bottom: 0;
                color: #00D25B;
            }

            p {
                margin-top: 0;
                color: #6c7293;
            }
        }

        .card-reporte {
            display: flex;
            gap: 2rem;

            @include Pantallas (820px) {
                flex-direction: column;
            }

            .grafica,
            .reporte,
            .reporte_año {
                background-color: #191C24;
                border-radius: 0.25rem;
                padding: 2rem;
                width: 100%;

                h2 {
                    text-transform: capitalize;
                    margin-top: 0;
                    // width: 90%;
                    // text-align: start;
                }
            }

            .grafica {
                flex-basis: 40%;
                margin: 0 auto;
                place-items: center;
                display: flex;
                flex-direction: column;
            }

            .reporte {
                flex-basis: 60%;
            }
        }

        .navegacion-aside {
            display: block;
            flex-direction: column;
            gap: 1rem;
            padding: 1rem 1.5rem 3rem 1.5rem;
            margin-top: .5px;

            .detalle {
                color: $blanco;
                position: relative;
                background-color: rgb(15, 16, 21);
                padding: 1.5rem 1rem;
                border-radius: 0 2rem 2rem 0;

                summary {
                    font-size: 1.8rem;
                    &::before {
                        content: "";
                        width: 3px;
                        height: 100%;
                        background: #0090e7;
                        display: inline-block;
                        position: absolute;
                        left: 0;
                        top: 0;
                    }
                }


                a {
                    display: block;

                    &:hover {
                        color: $verde;
                    }
                }

                @include Pantallas(1000px) {
                    .nav {
                        display: flex;
                        width: 100%;
                        text-align: center;
                        align-content: center;
                        justify-content: space-around;
                        padding: 0;
                        margin-bottom: 0;
                    }
                }

                @include Pantallas(300px) {
                    .nav {
                        flex-direction: column;
                    }
                }
            }

            @include Pantallas (1000px) {
                .detalle {
                    display: flex;
                    flex-direction: column;

                    a {
                        display: inline-block;
                    }
                }
            }

            @include Pantallas (1000px) {
                height: 100%;
            }
        }
    }

    .padding {
        padding: 2rem;
    }

    .mylegend {
        margin-top: 2rem;
        color: #FFAB00;
    }
}

.citas-mes,
.servicio-solicitado,
.ganancias-totales {
    text-align: left;
    margin-top: 1rem;
    padding: 1rem 2rem;
    background: #12151e;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 0.25rem;

    @include Pantallas (320px) {
        flex-direction: column;
    }
}

.card-titulo {
    background-color: #191C24;
    width: 100%;
    border-radius: 0.25rem;
    padding: 2rem;
    margin: 0;
}

#myCanvas {
    max-width: 100%;
}

