@use "base/variables" as *;
@use "base/mixins" as *;

.contacto {
  background-color: $gris2;
  padding: $separacion5 0;
  position: relative;

  .mensaje{
    background-color: $verde;
    margin: 0 auto;
    position: absolute;
    top: 3%;
    right: 3%;
    padding: 0 1rem;
  }

  iframe{
    margin: 2rem 0;
    width: 100%;
    max-width: 90%;
    text-align: start;
    height: 21rem;
    display: flex;
    @include Pantallas ($tablet){
      max-width: 100%;
      height: 35rem;
    }
  }
}

.contenedor-contacto {
  text-align: center;
  @include Pantalla(768px) {
    @include grid(2, 3rem);
  }
  h3 {
    color: $negro;
    margin-bottom: $separacion3;
    text-transform: uppercase;
    font-size: 2rem;
    text-align: start;
  }
  p {
    color: $negro;
    text-align: start;
    display: block;
    line-height: 2.2rem;
    margin: 0;
  }
  @include Pantallas(768px) {
    .margin-top {
      margin-top: $separacion4;
    }
  }
}

#formulario {
  text-align: center;
  .campo {
    display: block;
    width: 100%;
    padding: 0.7rem 0.7rem;
    margin-bottom: 0.8rem;
    border: 1px solid $negro;
    outline: none;
    border-radius: 0.3rem;
    font-weight: $regular;
    &::placeholder {
      color: $negro;
      font-weight: $regular;
    }
  }
  .campo {
    background-color: transparent;
    color: $negro;
    font-size: 2rem;
  }
}

.campo-field {
  height: 15rem;
  resize: none;
  margin-bottom: 3rem !important;
}

.mensaje-error {
  .campo {
    border: 3px solid rgba(255, 0, 0);
  }
}

.campos-validacion {
  position: relative;
  align-items: center;
  display: flex;
  .mensaje-error,.mensaje-correcto{
    position: absolute;
    top: 14%;
    right: 1%;
    padding: 1rem;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    width: 2.5rem;
    height: 2.5rem;
  }
  .mensaje-error {
    background-image: url(../img/cancel_48px.png);
    .campo {
      border: 3px solid rgba(255, 0, 0);
    }
  }
  .mensaje-correcto {
    background-image: url(../img/checkmark_64px.png);
  }
}

.mensaje p{
  color: $blanco;
}

.error p{
  color: $rojo;
}
