@use "base/variables"as *;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    overflow-x: hidden;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    user-select: none;
    font-family: $fuenteprincipal;
    font-size: 2rem;

    &::-webkit-scrollbar {
        width: .8rem;
        background-color: $gris5;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $gris2;
        border-radius: 1rem;
    }
}

p {
    color: $blanco;
    text-align: center;
}

.contenedor {
    width: 90%;
    max-width: 1300px;
    margin: 0 auto;
}

.section {
    padding-top: $separacion3;
    padding-bottom: $separacion3;
}

a {
    text-decoration: none;
    font-size: 2rem;
    color: $blanco;
}

img {
    width: 100%;
    max-width: 100%;
}

h1,
h2,
h3 {
    text-align: center;
    color: $blanco;
    font-family: $titulos;
}

h1 {
    font-size: 3.4rem;
}

h2 {
    font-size: 2.9rem;
    text-transform: uppercase;
}

h3 {
    font-size: 2.4rem;
    color: $blanco;
}

h4 {
    font-size: 2rem;
    text-align: start;
    margin: 1rem 0;
    color: $blanco;
}

li {
    color: $blanco;
    list-style: none;
}

button {
    text-transform: uppercase;
    cursor: pointer;
}

small {
    display: block;
    margin-bottom: 2rem;
}