@use "base/variables"as *;
@use "base/mixins"as *;

.invalidar-cita {
  text-align: center;
  margin: $separacion2 0;
  text-transform: uppercase;
  font-weight: $bold;
  color: #e70007;
  padding: 1rem;
  border: 1px solid #e70007;
}

.resumen {
  margin: 0 auto;
  padding: 2rem 0 0 0;
  border-radius: 0.5rem;
  max-width: 60rem;

  h3 {
    color: $blanco;
    // text-transform: uppercase;
    margin: 3rem 0;
    border-bottom: 1px solid $blanco;
  }

  p {
    color: $blanco;
    margin: 0;
  }

  .total span {
    font-weight: 400 !important;
  }

  h3,
  p {
    text-align: start;
  }

  .negrita {
    font-weight: 600;
    // text-transform: uppercase;
    margin-right: 1rem;
  }

  .contenedor-servicio {
    margin: 0;
    display: flex;
    justify-content: space-between;
  }

  .total {
    margin: 2rem 0 0 0;
    text-align: end;
    font-weight: $bold;
    border-top: 1px solid $blanco;
    padding-top: 2rem;
    border-bottom: none;

    span {
      font-weight: 400 !important;
    }

  }
}

.servicios_contenedor {
  // margin-bottom: 8rem;
  @extend .resumen;
  li, .acciones{
    display: flex;
    justify-content: space-between;
  }

  li {
    margin: 2rem 0;
    padding-top: 2rem;
    border-top: 1px solid $blanco;
    // border-bottom: 1px solid $blanco;
  }
  p{
    font-weight: $bold;
    span {
      font-weight: 400 !important;
      margin-left: 1rem;
    }
  }
  .acciones{
    gap: 1rem;
  }
}

.btn-reservar {
  cursor: pointer;
  font-weight: 500;
  transition: all .2s;
  padding: 1rem 2rem;
  background: $blanco;
  border: 1px solid transparent;
  display: block;
  align-items: center;
  width: 100%;
  max-width: 60rem;
  margin: 3rem auto 5rem auto;

  &:hover {
    background: $negro;
    color: $blanco;
  }
}

.no-visible {
  visibility: hidden;
  display: none;
}

.swal2-title {
  font-size: 3.5rem !important;
}

.swal2-html-container {
  font-size: 2rem !important;
}

.swal2-styled.swal2-confirm, .swal2-deny, .swal2-styled {
  background-color: $negro !important;
  font-size: 2rem !important;
}

.swal2-icon.swal2-success [class^=swal2-success-line] {
  background-color: $negro !important;
}
