@use "base/variables" as *;
@use "base/mixins" as *;

body {
  background-color: $negro;
}

.contenedor-estetica {
  height: 100%;
  text-align: center;
  place-items: center;
  .imagen {
    position: fixed;
    height: 100%;
    width: 100%;
    background-image: url(../img/11.webp);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    opacity: 20%;
    filter: blur(8px);
    .overlay{
      position: relative;
      background-color: rgb(0, 0, 0, 0.2);
      // opacity: 20%;
      width: 100%;
      height: 100%;
    }
  }
}

.app {
  position: relative;
  .header2 {
    margin: 2rem;
    text-transform: uppercase;
  }

  h2 {
    text-transform: capitalize;
    margin-top: $separacion3;
  }
  
  .seccion {
    display: none;
    margin: 4rem 0 0 0;
  }
}

.mostrar-seccion {
  display: block !important;
}
