@use "base/variables"as *;
@use "base/mixins"as *;

.formulario-cliente {
  max-width: 60rem;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  flex-direction: column;

  input::-webkit-calendar-picker-indicator {
    filter: invert(1);
  }
  .inputContainer {
    margin: 2rem 0;
  }
}

.formulario-login,
.formulario-cliente {
  .inputContainer {
    input {
      &:read-only {
        cursor: not-allowed;
        color: #dadce0;

        &:focus {
          outline: none !important;
          border-bottom: 1px solid $blanco;
          padding: 1rem 1rem 0.7rem 0;
        }
      }
    }
  }
}