@use "base/variables" as *;
@use "base/mixins" as *;

.listado-servicios {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 4rem 0;
  @include Pantalla($tablet) {
    @include grid(2, 1rem);
  }

  .servicio {
    // user-select: none; 
    border-radius: 0.5rem;
    border: 1px solid $blanco;
    cursor: pointer;
    padding: 1rem;
    transition-property: transform;
    transition-duration: 300ms;

    .precio-servicio {
      font-weight: 600;
    }

    .nombre-servicio, .precio-servicio {
      text-align: center;
      margin: 0;
    }

    &.seleccionado {
      background-color: $blanco;
      transform: scale(0.90);
      transition: all 0.3s ease;
      p {
        color: $negro;
      }
    }

    &:hover{
      transform: scale(0.90);
    }
  }
}

.flex-perfil{
  .eliminar-cuenta {
    transition: all .2s;
    padding: 1rem;
    background-color: $blanco;
    border: 1px solid $negro;
    display: block;
    border-radius: .3rem;
    font-size: 1.8rem;

    &:hover {
        background-color: transparent;
        color: $blanco;
        border: 1px solid $blanco;
    }
  }
}