@use 'base/variables' as *;

.boton-actualizar, .boton-eliminar{
    color: $negro;
    border: none;
    height: 100%;
    width: 13rem;
    justify-content: center;
    @extend .btn-fecla;
    font-weight: normal !important;
    font-size: 1.8rem !important;
}

.main_div,
.main_div--blanco {
    position: relative;
    z-index: 0;
    display: inline-block;
}

.main_div::before {
    content: '';
    position: absolute;
    width: 30px;
    height: 30px;
    background: transparent;
    top: -0.5rem;
    left: -0.5rem;
    z-index: -5;
    border-top: 3px solid $negro;
    border-left: 3px solid $negro;
    transition: 0.5s;
}

.main_div::after {
    content: "";
    position: absolute;
    width: 30px;
    height: 30px;
    background: transparent;
    bottom: -0.5rem;
    right: -0.5rem;
    z-index: -5;
    border-right: 3px solid $negro;
    border-bottom: 3px solid $negro;
    transition: 0.5s;
}

.main_div:hover::before {
    width: 100%;
    height: 100%;
}

.main_div:hover::after {
    width: 100%;
    height: 100%;
}

.main_div button,
.boton,
.main_div input,
.main_div a {
    padding: 0.7em 2em;
    font-size: 1.6rem !important;
    font-weight: 500;
    background: $negro;
    color: $blanco;
    border: none;
    text-transform: uppercase;
    cursor: pointer;
}

.main_div--blanco a,
.main_div--blanco button,
.main_div--blanco input {
    background: $blanco;
    color: $negro;
}

.main_div--blanco::before {
    border-top: 3px solid $blanco;
    border-left: 3px solid $blanco;
}

.main_div--blanco::after {
    border-right: 3px solid $blanco;
    border-bottom: 3px solid $blanco;
}

//----------------
.btn-fecla {
    cursor: pointer;
    font-weight: 700;
    transition: all .2s;
    padding: .5rem 2rem;
    background: $blanco;
    border: 1px solid transparent;
    display: flex;
    align-items: center;
    font-size: 15px;

    &:hover {
        background: $negro;
        color: $blanco;
    }

    &>svg {
        width: 34px;
        margin-left: 10px;
        transition: transform .3s ease-in-out;
    }

    &:hover svg {
        transform: translateX(5px);
    }

    &:active {
        transform: scale(0.95);
    }
}

.btn-fecha-izquierda {
    cursor: pointer;
    font-weight: 700;
    transition: all .2s;
    padding: .5rem 2rem;
    background: $blanco;
    border: 1px solid transparent;
    display: flex;
    align-items: center;
    font-size: 15px;

    &:hover {
        background: $negro;
        color: $blanco;
    }

    &>svg {
        width: 34px;
        margin-left: 0;
        transition: transform .3s ease-in-out;
    }

    svg {
        transform: rotate(180deg);
    }

    &:hover svg {
        transform: translateX(-5px) rotate(180deg);
    }

    &:active {
        transform: scale(0.95);
    }
}

//--
.icon-btn {
    width: 50px;
    height: 50px;
    border: 1px solid transparent;
    background: white;
    overflow: hidden;
    position: relative;
    transition: width 0.2s ease-in-out;
}

.add-btn:hover {
    width: 170px;
}

.add-btn::before,
.add-btn::after {
    transition: width 0.2s ease-in-out, border-radius 0.2s ease-in-out;
    content: "";
    position: absolute;
    height: 4px;
    width: 10px;
    top: calc(50% - 2px);
    background: $negro;
}

.add-btn::after {
    right: 14px;
    overflow: hidden;
    border-top-right-radius: 2px;
    border-bottom-right-radius: 2px;
}

.add-btn::before {
    left: 14px;
    border-top-left-radius: 2px;
    border-bottom-left-radius: 2px;
}

.icon-btn:focus {
    outline: none;
}

.btn-txt {
    opacity: 0;
    transition: opacity 0.2s;
    line-height: 2rem;
}

.add-btn:hover::before,
.add-btn:hover::after {
    width: 4px;
    border-radius: 2px;
}

.add-btn:hover .btn-txt {
    opacity: 1;
}

.add-icon::after,
.add-icon::before {
    transition: all 0.2s ease-in-out;
    content: "";
    position: absolute;
    height: 20px;
    width: 2px;
    top: calc(50% - 10px);
    background: $negro;
    overflow: hidden;
}

.add-icon::before {
    left: 22px;
    border-top-left-radius: 2px;
    border-bottom-left-radius: 2px;
}

.add-icon::after {
    right: 22px;
    border-top-right-radius: 2px;
    border-bottom-right-radius: 2px;
}

.add-btn:hover .add-icon::before {
    left: 15px;
    height: 4px;
    top: calc(50% - 2px);
}

.add-btn:hover .add-icon::after {
    right: 15px;
    height: 4px;
    top: calc(50% - 2px);
}