@use "base/variables"as *;
@use "base/mixins"as *;

.horarios {
  background-image: url(../img/presentacion2.webp);
  position: relative;
  height: 65rem;
  max-height: 65rem;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  text-align: center;

  .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
  }

  h3 {
    text-transform: uppercase;
    margin-top: $separacion4;
  }

  .overlay-descripcion {
    margin: $separacion2 auto $separacion4 auto;

    @include Pantalla(768px) {
      width: 60%;
    }
  }
}

.contenedor-dias {
  ul {
    padding: 0;
    width: 93%;
    margin: 0 auto;
    @include grid(2, $separacion2);

    @include Pantalla ($tablet) {
      @include grid(6, $separacion2);
    }


    li {
      padding: $separacion1 0;

      @include Pantalla(800px) {
        padding: $separacion2 0;
      }

      border: 0.1rem solid $blanco;
      font-size: $separacion2;
      font-weight: $bold2;

      @include Pantallas(300px) {

        li,
        p {
          font-size: 1.5rem;
        }
      }

      p {
        font-weight: $delgado;
        margin: 0;
      }
    }
  }
}