@use "base/variables" as *;
@use "base/mixins" as *;

.text-center {
    text-align: center;
}

.text-justify{
    text-align: justify;
}

.no-margin {
    margin: 0 !important;
}

.margin{
    margin-bottom: 8rem !important;
}

.flex {
    display: flex;
    gap: 2rem;
    @include Pantallas($telefono){
      flex-direction: column;
      gap: 0;
    }
}
  
.margin-bottom{
    margin: 0 !important;
    margin-bottom: 3rem !important;
}

.margin-top{
    margin-top: 4rem !important;
}

.verde {
    color: #00D25B !important;
}

.gris{
    color: #6C7293 !important;
}

.blanco {
    color: $blanco !important;
}

.no-padding-bottom{
    padding-bottom: 0 !important;
}