@use "base/variables" as *;
@use "base/mixins" as *;

.tabs {
  // margin-bottom: 1rem;
  width: 100%;
  @include grid(3, 1rem);

  @include Pantallas ($tablet) {
    @include grid(1, 1rem);
  }

  button {
    position: relative;
    background-color: transparent;
    color: $blanco;
    padding: .5rem 0.3rem;
    border: 1px solid $blanco;
    text-transform: capitalize;

    // font-weight: $bold2;
    &.actual {
      background-color: $blanco;
      color: $negro;
    }

    &:hover {
      background-color: $blanco;
      color: $negro;
      transition: all 0.3s ease;
    }
  }
}

.paginacion {
  display: flex;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin: 2rem 0;

  button {
    &.ocultar {
      display: none;
    }
  }

  @include Pantallas($telefono) {
    flex-direction: column;
  }
}

@include Pantallas(530px) {
  .reservas {
    display: block;
    overflow-x: auto;
  }
}

@include Pantallas(900px) {
  .reservas-tamaño-completo {
    display: block;
    overflow-x: auto;
  }
}

.contenedor-reservas {
  position: relative;
  z-index: 1;
  margin: 0 auto;
  width: 100%;
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  place-items: center;

  h1 {
    text-transform: uppercase;
    margin-bottom: 5rem;
  }
}

.reservas {
  margin: 0 auto;
  margin-bottom: 2rem;
  width: 100%;
  max-width: 90%;
  border-spacing: 0;

  thead {
    width: 100%;
    background: $blanco;

    th {
      color: $negro;
      padding: 1rem 2rem;
      text-transform: uppercase;
    }
  }

  tbody {
    width: 100%;

    td {
      text-align: center;
      color: $blanco;
      line-height: 2.4rem;
      padding: 1rem 0;
      border-bottom: 1px solid $blanco;
      font-size: 1.8rem;
    }

    tr {
      opacity: 100;

      &:hover {
        background-color: rgb(255, 255, 255, 0.1);
        backdrop-filter: blur(3px);
      }
    }
  }

  &::-webkit-scrollbar:horizontal {
    width: 0.5px;
    height: 4px;
  }

  &::-webkit-scrollbar {
    background-color: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $blanco;
    border-radius: .5rem;
  }
}

.contenedor-usuario {
  display: block;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;

  .usuario {
    background-color: transparent;
    color: $blanco;
    border: none;
    display: block;
    width: 100%;
  }

  label {
    color: #dadce0;
    font-size: 1.4rem;
    top: -12px;
    left: 0;
    display: table-cell
  }
}

.contenedor-reservaciones {
  display: flex;
  gap: 2rem;
  margin: 0 0 3rem 0;
  flex-wrap: wrap;
  justify-content: center;

  @include Pantallas($telefono) {
    flex-direction: column;
    align-content: center;
  }

  .reservacion {
    border-radius: .5rem;
    padding: 1rem 2rem;
    display: inline-block;
    background-color: rgba(41, 39, 39, .5);
    box-shadow: 0 0 30px -5px $negro;
    max-width: 34rem;
    width: 100%;

    p {
      text-align: start;
      margin: .5rem 0;
    }

    h4 {
      text-decoration: underline;
    }

    .titulo-reserva {
      font-size: 2rem;
      font-weight: 800 !important;
      margin: 1rem 0;
      text-align: center;
      text-transform: uppercase;
    }

    .linea{
      width: 100%;
      position: relative;
      &::before{
        content: '';
        border-top: 1px solid #fff;
        z-index: 0;
        width: 20%;
        max-width: 24%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 50%;
      }

      &::after{
        content: '';
        border-top: 1px solid #fff;
        z-index: 0;
        width: 20%;
        max-width: 24%;
        height: 100%;
        position: absolute;
        right: 0;
        top: 50%;
      }
    }
  }

  .negrita {
    margin: 0 !important;
    font-weight: 500;
  }

  .total {
    border-bottom: none;
    border-top: 1px solid $blanco;
    font-weight: 700;
    margin: 2rem 0 0 0;
    padding-top: 1rem;
    text-align: end;
  }
}

.flex {
  justify-content: space-between;
  // align-items: center;

  @include Pantallas($telefono) {
    flex-direction: row;
  }
}