@use "base/variables"as *;
@use "base/mixins"as *;

.contenedor-paginaerror {
    padding: 2rem 0;
    background: #3AA3FF;
    width: 100%;
    height: 100vh;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    .mensaje-paginaerror {
        flex-basis: 40%;
        background-color: $blanco;
        padding: 1rem 2rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: flex-start;

        @include Pantallas($tablet) {
            padding: 4rem 2rem 6rem 2rem;
            flex-basis: 100%;
        }

        h1 {
            font-weight: $bold;
            font-size: 10rem;
            margin-bottom: 0;
            line-height: 1;
        }

        h3 {
            font-size: 2.5rem;
            line-height: 2.5rem;
        }

        h1,
        h3,
        p {
            text-align: start;
            color: $negro;
        }

        p {
            line-height: 2.3rem;
            margin: 1rem 0 4rem 0;
        }

        a {
            background-color: $negro;
            color: $blanco;
            cursor: pointer;
            border: none;
            padding: 1rem;
            width: 50%;
            text-transform: uppercase;
            border: 1px solid $negro;
            text-align: center;
            line-height: 1.4rem;
            font-size: 1.6rem;

            @include Pantallas($telefono) {
                width: 100%;
            }

            &:hover {
                transition: all 0.3s;
                background-color: transparent;
                color: $negro;
            }
        }
    }

    .imagen-paginaerror {
        flex-basis: 60%;

        @include Pantallas($tablet) {
            display: none;
            visibility: hidden;
        }
    }
}

.contenedor-error {
    position: fixed;
    top: 3%;
    right: 1%;
    z-index: 1;
    display: flex;
    flex-direction: column;
    line-height: 1.8rem;

    p {
        color: $blanco;
        margin: 0;
        max-width: 28rem;
    }
}

.alerta {
    padding: 1.5rem 2rem;
    text-align: center;
    margin: 0.5rem 0;
    border-radius: 2rem;
    transition: all 0.3s;

    &.error {
        background-color: #da2f3e;
        color: $blanco;
        display: block;
    }

    &.exito {
        background-color: $verde !important;
        color: $blanco;
        display: block;
    }

    p:nth-child(2) {
        margin-top: 0.5rem;
    }

    &.oculto {
        transition: all 0.3s;
        display: none;
        visibility: hidden;
    }
}