@use "base/variables"as *;
@use "base/mixins"as *;

.contenedor-login {
  background-image: url(../img/11.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 100vh;
  position: relative;
  filter: blur(8px);

  &::before {
    content: '';
    background-color: rgba(0, 0, 0, 0.6);
    width: 100%;
    height: 100vh;
    position: absolute;
  }
}

.contenedor-tyc-izquierda {
  margin: 0;
}

.login {
  background-color: rgba(41, 39, 39, 0.5);
  border-radius: 1rem;
  max-width: 80%;
  box-shadow: 0px 0px 30px -5px $negro;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 0;

  legend,
  label {
    color: $blanco;
  }

  legend {
    text-transform: capitalize;
  }
}

.formulario-login {
  text-align: center;
  place-items: center;
  margin: 0 auto;
  padding: 4rem 0;
  position: relative;
  @include Pantallas ($telefono){
    padding: 1rem 0;
  }

  fieldset {
    border: none;
    margin: 0 auto;
    width: 90%;
    max-width: 45rem;
  }

  legend {
    background-color: transparent;
    font-size: 3.9rem;
    text-transform: none;
    line-height: 3rem;
    letter-spacing: 0.3rem;
    font-weight: $regular;
    margin-bottom: 5.3rem;
    @include Pantallas ($telefono){
      margin-bottom: 1rem;
      font-size: 3rem;
    }
  }

  .imagen-login,
  source {
    max-width: 14rem;
    margin: 0 auto;
    margin-bottom: 1rem;
    @include Pantallas ($telefono){
      max-width: 10rem;
    }
  }

  select option {
    color: $negro;
  }

  label {
    margin-top: 3rem;
    margin-bottom: 0.8rem;
    color: #465a65;
  }

  .input__modificado {
    display: block;
    border: none;
    width: 100%;
    // height: 100%;
    border-bottom: 1px solid $blanco;
    padding: 1rem 1rem 0.7rem 0;
    margin-bottom: 2rem;
    background-color: transparent;
    color: $blanco;

    &::placeholder {
      color: $blanco;
    }

    &:focus {
      outline: none !important;
      border-bottom: 2px solid $negro;
      padding: 1rem 1rem 0.7rem 0;
    }
  }

  .contenedor-div {
    gap: 3rem;
    margin-top: 2rem;
    align-content: center;
    place-items: center;
    @include Pantallas ($telefono){
      margin: 0;
      gap: 2rem;
    }
  }

  .contenedor-div,
  .no-gap {
    display: flex;
    flex-direction: column;
  }
}

.contenedor-ver {
  display: flex;
  position: relative;
  margin: 0 !important;

  label {
    margin: 0;
    position: absolute;
    right: 0;
    z-index: 1;

    img {
      width: 2.5rem;
      height: 2.5rem;
      margin-bottom: -1.8rem;
    }
  }
}

.no-gap {
  gap: 1rem;
  margin-top: 0;

  a {
    font-size: 1.8rem;

    &:hover {
      color: $negro;
    }
  }
}

.flex {
  .flex-login {
    background-image: url(../img/11.jpg);
    border-bottom-left-radius: 1rem;
    border-top-left-radius: 1rem;
    background-size: cover;
    width: 100%;
    background-position: center center;
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    flex-basis: 50%;
    text-align: start !important;

    &::before {
      content: '';
      border-bottom-left-radius: 1rem;
      border-top-left-radius: 1rem;
      background-color: rgb(0, 0, 0, 0.5);
      width: 100%;
      height: 100%;
      position: absolute;
    }

    p {
      text-align: start;
      margin: 0;
      margin-top: 1rem;
      padding-right: 4rem;
    }

    h3 {
      margin: 0;
    }

    h1 {
      margin-bottom: 10rem;
      font-size: 4rem;
    }

    h1,
    h3,
    p,
    .footer__networks {
      padding-left: 4rem;
      z-index: 1;

      a:hover {
        background-color: transparent;
        transform: none;
        filter: invert(1);
      }
    }

    @include Pantallas($tablet) {
      display: none;
      visibility: hidden;
    }
  }

  .flex-formulario {
    flex-basis: 50%;

    @include Pantallas($tablet) {
      flex-basis: 100%;
    }
  }
}

.contenedor-boton {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 3rem;
}

.inputContainer {
  position: relative;
  width: 100%;
  height: 4.5rem;
  margin-bottom: 1.7rem;
  text-align: start;

  .input {
    position: absolute;
    outline: none;
    z-index: 1;

    &::placeholder {
      color: transparent;
    }

    &:focus+.label {
      top: -12px;
      z-index: 1;
      color: #DADCE0;
      font-size: 1.4rem;
    }

    &:not(:placeholder-shown)+.label {
      top: -12px;
      z-index: 1;
      color: #DADCE0;
      font-size: 1.4rem;
    }
  }

  .label {
    position: absolute;
    top: 15px;
    left: 0;
    color: $blanco;
    transition: 0.5s;
    z-index: 0;
    margin: 0;
  }
}

.validaciones-contraseña {
  margin: -1rem 0 3rem 0.5rem;

  p {
    text-align: start;
    line-height: 1.5rem;
    margin: 0;
    font-size: 1.4rem;
    color: #DADCE0;
  }
}

.confirmar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10rem 1rem 5rem 1rem;
  text-align: center;

  h1 {
    font-weight: $bold2;
    font-size: 3.9rem;
  }

  h2 {
    background-color: transparent;
    text-transform: none;
    font-size: 3.2rem;
    line-height: 3rem;
    font-weight: $bold;
    margin-bottom: 2rem;
  }

  h1,
  p {
    margin: 0;
  }

  p {
    width: 90%;
    text-align: center;
    margin: 0 auto;
  }
}

.token-rojo {
  background-color: $rojo;
}

.token-verde {
  background-color: $verde !important;
}

.token-rojo,
.token-verde {
  padding: 1rem;
  text-transform: uppercase;
  max-width: 90%;
  margin: 0 auto 3rem auto;
  border-radius: .5rem;
  font-weight: $regular;
}

.ocultar-formulario{
  display: none;
  visibility: hidden;
}