@use "base/variables"as *;
@use "base/mixins"as *;

.footer {
  .derechos {
    padding: 1.5rem 0;

    p {
      align-items: center;
      display: flex;
      justify-content: center;
      margin: 0;

      @media (max-width: 510px) {
        font-size: 1.2rem;
      }
    }
  }

  .corazon {
    animation: heart 2s ease infinite;
    -webkit-animation: heart 2s ease infinite;
    height: 1.5rem;
    margin: 0 0.5rem;
    width: 1.5rem;
    display: inline-block;
  }

  @-webkit-keyframes heart {
    0% {
      transform: scale(1);
    }

    10% {
      transform: scale(1.2);
    }
  }

  @keyframes heart {
    0% {
      transform: scale(1);
    }

    10% {
      transform: scale(1.2);
    }
  }
}

.copyright {
  padding-bottom: 0.1rem;
}

.footer__networks {
  text-align: center;
  padding: 2rem 0 1rem 0;

  a {
    display: inline-flex;
    height: 3rem;
    width: 3rem;
    padding: 0.4rem;
    border-radius: 50%;
    transition: transform 0.3s;

    &:hover {
      background-color: $gris5;
      transform: scale(1.2);
    }
  }
}