@use "base/variables"as *;
@use "base/mixins"as *;

.header {
  position: absolute;
  display: block;
  width: 100%;
  background: rgba(0, 0, 0, 0);
  transition: 0.5s ease all;

  .contenido-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: $separacion1 0;

    @include Pantallas(768px) {
      flex-direction: column;
    }

    h1 {
      font-weight: $bold;
      margin: 0 0 .5rem 0;
      cursor: pointer;
      font-size: 4rem;
    }
  }

}

.navegacion-principal {
  transition: 0.5s ease all;
  @include grid(3, $separacion2);

  @include Pantallas ($telefono) {
    @include grid(3, 1rem);

    a {
      font-size: 1.7rem;
    }
  }

  a {
    position: relative;

    &:hover::after {
      width: 100%;
    }

    &::after {
      content: " ";
      position: absolute;
      display: flex;
      width: 0;
      height: 2px;
      bottom: 0;
      background: $negro;
      transition: 0.5s ease all;
    }
  }
}

.navegacion-principal.sticky {
  padding: 0 5px;
  transition: 0.5s ease all;

  &::before {
    content: "";
    bottom: 0;
    box-shadow: 0 0.2rem 0.3rem $negro;
    height: 0.1rem;
    left: 0;
    position: absolute;
    right: 0;
  }
}

.header.sticky {
  position: fixed;
  z-index: 1;
  background: $negro;

  h1 {
    padding: 0 5px;
    transition: 0.5s ease all;
  }
}