@use "base/variables" as *;
@use "base/mixins" as *;

.perfil {
    align-items: center;
    text-align: center;
    padding: 3rem;
    margin: 0 auto;
    max-width: 70rem;

    .logo {
        margin: 0 auto 3rem 0;
        display: flex;
        align-items: flex-end;
        justify-content: flex-start;
        gap: 1rem;
    }

    img {
        max-width: 4rem;
    }

    h1 {
        font-size: 3.9rem;
        font-weight: $bold;
    }

    h2 {
        text-transform: capitalize;
    }

    h1,
    h2,
    h3 {
        margin: 0;
    }

    h3 {
        text-align: center;
    }

    .izquierda {
        text-align: start;
    }

    .contenedor-hacer {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;
        margin: 3rem auto 0 auto;
        max-width: 70rem;

        @include Pantallas($tablet) {
            grid-template-columns: none;
        }

        a {
            border-radius: .5rem;
            padding: 1rem;
            background-color: rgba(41, 39, 39, .5);
            border-radius: .3rem;
            box-shadow: 0 0 30px -5px #000;

            section,
            button {
                h3 {
                    color: $blanco;
                }

                lord-icon {
                    filter: invert(1);
                }
            }
        }

        .sin-padding {
            padding: 0 !important;

            button {
                background-color: transparent;
                cursor: pointer;
                border: none;
                padding: 1rem;
                width: 100%;
                text-transform: capitalize;
            }
        }
    }
}

.centrado {
    cursor: pointer;
}

.swal2-actions {

    .swal2-confirm,
    .swal2-styled,
    .swal2-default-outline {
        background-color: $verde  !important;
        font-size: 2rem !important;
    }

    .swal2-cancel {
        background-color: $rojo  !important;
    }

    .swal2-icon.swal2-success [class^=swal2-success-line] {
        background-color: $negro  !important;
    }


}

.swal2-popup {
    width: 50rem !important;
    max-width: 100% !important;
}

.usuario_oculto {
    display: none;
    visibility: hidden;
}