@use "base/variables" as *;
@use "base/mixins" as *;

.presentacion {
  background-image: url(../img/presentacion4.jpg);
  background-repeat: no-repeat;
  background-position: center right;
  background-size: cover;
  background-attachment: fixed;
  height: 67rem;
  max-height: 67rem;
  display: flex;
  justify-content: space-evenly;
  place-items: center;
  flex-direction: column;
}

.overlay-presentacion {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 2.5rem;
  h2, p {
    margin-bottom: 0;
  }
  h2{
    font-weight: $bold2;
    font-size: 3.5rem;
  }
}

.overlay-informacion {
  display: flex;
  place-items: center;
  background-color: rgb(0, 0, 0, 0.8);
  padding: $separacion1 0;
  display: flex;
  flex-direction: column;
  @include Pantalla(768px) {@include grid(3, $separacion1);}
}

.detalle a{
  display: flex;
  padding: 0.5rem 0;
  cursor: pointer;
  place-items: center;
  img {
    margin-right: 0.8rem;
    max-width: 5rem;
    @include Pantallas(767px) {
      max-width: 3.5rem;
    }
  }
}
