@use "base/variables"as *;
@use "base/mixins"as *;

.servicios {
  background-color: $gris2;
  padding: 2.5rem 0;
  text-align: center;
  padding-bottom: 6rem;

  h2,
  h3,
  p {
    color: $negro;
  }
}

.contenedor-servicios {
  margin-bottom: 6rem;
  @include Pantalla ($tablet) {
    @include grid(3, 1rem);
  }

  @include grid(2, 2rem);

  @include Pantallas ($telefono) {
    display: flex;
    flex-direction: column;
  }

  .cortes {
    margin: 0;
    text-align: center;

    &:before {
      content: "";
      border-radius: 50%;
      width: 10rem;
      height: 10rem;
      display: block;
      margin: 0 auto;
      background-size: 5.6rem;
      background-repeat: no-repeat;
      background-position: center center;
    }
  }

  p {
    margin: $separacion1;
  }
}

.corte-cabello {
  &:before {
    background-image: url(../img/hairstyle.webp);
  }
}

.corte-barba {
  &:before {
    background-image: url(../img/beard.webp);
  }
}

.mascarilla {
  &:before {
    background-image: url(../img/cream.webp);
  }
}

.mascarilla-cera {
  &:before {
    background-image: url(../img/wax.webp);
  }
}