@use "base/variables" as *;
@use "base/mixins" as *;

.empleados {
  background-image: url(../img/presentacion.webp);
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
  background-attachment: fixed, scroll;
  position: relative;
  text-align: center;
  .overlay {
    position: absolute;
    background-color: rgb(0,0,0,.8);
    width: 100%;
    height: 100%;
  }
  .contenedor-empleados {
    padding: 4rem 0;
    h2 {
      color: $blanco;
      padding: $separacion1 0;
    }
  }
  .empleados-detalle {
    @include Pantalla(1250px) {
      margin: 0 9rem;
    }
    @include Pantalla(700px) {
      @include grid(3, $separacion3);
    }
    p {
      font-size: 2rem;
      font-weight: $bold2;
      margin: 1rem 0;
    }
    .empleados__detalle {
      max-height: 40rem;
      border-radius: 0.3rem;
      border: 0.5rem solid $blanco;
      &:hover {
        transform: scale(0.9);
        transition: all 0.5s;
      }
    }
    @include Pantalla(768px) {
      .empleados__detalle--izquierda,
      .empleados__detalle--derecha {
        transform: rotate(-3deg);
      }
      .empleados__detalle--centro {
        transform: rotate(3deg);
      }
    }
  }
  h2,
  p,
  .empleados__detalle,
  img {
    position: relative;
    width: auto;
  }
}

.empleados-redes {
  margin-bottom: 1rem;
  a {
    img {
      display: inline-block;
      max-width: 3rem;
      cursor: pointer;
      transition: all 0.2s;
      border-radius: 50%;
      &:hover {
        transform: scale(1.2);
      }
    }
  }
}
