@use "base/variables" as *;
@use "base/mixins" as *;

.boton-Top {
  position: fixed;
  z-index: -1;
  margin-right: 2rem;
  right: 2%;
  top: 88%;
  text-align: center;
  transition: 0.4s;
  box-shadow: 0 7px 4px -6px $blanco;
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  img {
    transition: 0.4s;
    transform: scale(0);
    border-radius: 50%;
    background-color: $negro;
  }
}

.imagen-Top {
  z-index: 1;
  transition: 0.3s;
  img {
    cursor: pointer;
    animation: popup 0.3s ease-in-out;
    animation: popup2 0.5s alternate infinite ease-in;
    transform: scale(1);
  }
}

@keyframes popup {
  0% {
    width: 0;
    height: 0;
  }
  50% {
    max-width: 4rem;
    max-height: 4rem;
  }
  100% {
    max-width: 6rem;
    max-height: 6rem;
  }
}

@keyframes popup2 {
  0% {
    transform: translateY(-20%);
  }
  100% {
    transform: translateY(0%);
  }
}
