.boton-actualizar, .boton-eliminar {
  color: #000;
  border: none;
  height: 100%;
  width: 13rem;
  justify-content: center;
  font-weight: normal !important;
  font-size: 1.8rem !important;
}

.main_div,
.main_div--blanco {
  position: relative;
  z-index: 0;
  display: inline-block;
}

.main_div::before {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  background: transparent;
  top: -0.5rem;
  left: -0.5rem;
  z-index: -5;
  border-top: 3px solid #000;
  border-left: 3px solid #000;
  transition: 0.5s;
}

.main_div::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  background: transparent;
  bottom: -0.5rem;
  right: -0.5rem;
  z-index: -5;
  border-right: 3px solid #000;
  border-bottom: 3px solid #000;
  transition: 0.5s;
}

.main_div:hover::before {
  width: 100%;
  height: 100%;
}

.main_div:hover::after {
  width: 100%;
  height: 100%;
}

.main_div button,
.boton,
.main_div input,
.main_div a {
  padding: 0.7em 2em;
  font-size: 1.6rem !important;
  font-weight: 500;
  background: #000;
  color: #FFFFFF;
  border: none;
  text-transform: uppercase;
  cursor: pointer;
}

.main_div--blanco a,
.main_div--blanco button,
.main_div--blanco input {
  background: #FFFFFF;
  color: #000;
}

.main_div--blanco::before {
  border-top: 3px solid #FFFFFF;
  border-left: 3px solid #FFFFFF;
}

.main_div--blanco::after {
  border-right: 3px solid #FFFFFF;
  border-bottom: 3px solid #FFFFFF;
}

.btn-fecla, .boton-actualizar, .boton-eliminar {
  cursor: pointer;
  font-weight: 700;
  transition: all 0.2s;
  padding: 0.5rem 2rem;
  background: #FFFFFF;
  border: 1px solid transparent;
  display: flex;
  align-items: center;
  font-size: 15px;
}
.btn-fecla:hover, .boton-actualizar:hover, .boton-eliminar:hover {
  background: #000;
  color: #FFFFFF;
}
.btn-fecla > svg, .boton-actualizar > svg, .boton-eliminar > svg {
  width: 34px;
  margin-left: 10px;
  transition: transform 0.3s ease-in-out;
}
.btn-fecla:hover svg, .boton-actualizar:hover svg, .boton-eliminar:hover svg {
  transform: translateX(5px);
}
.btn-fecla:active, .boton-actualizar:active, .boton-eliminar:active {
  transform: scale(0.95);
}

.btn-fecha-izquierda {
  cursor: pointer;
  font-weight: 700;
  transition: all 0.2s;
  padding: 0.5rem 2rem;
  background: #FFFFFF;
  border: 1px solid transparent;
  display: flex;
  align-items: center;
  font-size: 15px;
}
.btn-fecha-izquierda:hover {
  background: #000;
  color: #FFFFFF;
}
.btn-fecha-izquierda > svg {
  width: 34px;
  margin-left: 0;
  transition: transform 0.3s ease-in-out;
}
.btn-fecha-izquierda svg {
  transform: rotate(180deg);
}
.btn-fecha-izquierda:hover svg {
  transform: translateX(-5px) rotate(180deg);
}
.btn-fecha-izquierda:active {
  transform: scale(0.95);
}

.icon-btn {
  width: 50px;
  height: 50px;
  border: 1px solid transparent;
  background: white;
  overflow: hidden;
  position: relative;
  transition: width 0.2s ease-in-out;
}

.add-btn:hover {
  width: 170px;
}

.add-btn::before,
.add-btn::after {
  transition: width 0.2s ease-in-out, border-radius 0.2s ease-in-out;
  content: "";
  position: absolute;
  height: 4px;
  width: 10px;
  top: calc(50% - 2px);
  background: #000;
}

.add-btn::after {
  right: 14px;
  overflow: hidden;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}

.add-btn::before {
  left: 14px;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
}

.icon-btn:focus {
  outline: none;
}

.btn-txt {
  opacity: 0;
  transition: opacity 0.2s;
  line-height: 2rem;
}

.add-btn:hover::before,
.add-btn:hover::after {
  width: 4px;
  border-radius: 2px;
}

.add-btn:hover .btn-txt {
  opacity: 1;
}

.add-icon::after,
.add-icon::before {
  transition: all 0.2s ease-in-out;
  content: "";
  position: absolute;
  height: 20px;
  width: 2px;
  top: calc(50% - 10px);
  background: #000;
  overflow: hidden;
}

.add-icon::before {
  left: 22px;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
}

.add-icon::after {
  right: 22px;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}

.add-btn:hover .add-icon::before {
  left: 15px;
  height: 4px;
  top: calc(50% - 2px);
}

.add-btn:hover .add-icon::after {
  right: 15px;
  height: 4px;
  top: calc(50% - 2px);
}

.header {
  position: absolute;
  display: block;
  width: 100%;
  background: rgba(0, 0, 0, 0);
  transition: 0.5s ease all;
