  margin: 0 0 0.5rem 0;
  cursor: pointer;
  font-size: 4rem;
}

.navegacion-principal {
  transition: 0.5s ease all;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
@media (max-width: 480px) {
  .navegacion-principal {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
  .navegacion-principal a {
    font-size: 1.7rem;
  }
}
.navegacion-principal a {
  position: relative;
}
.navegacion-principal a:hover::after {
  width: 100%;
}
.navegacion-principal a::after {
  content: " ";
  position: absolute;
  display: flex;
  width: 0;
  height: 2px;
  bottom: 0;
  background: #000;
  transition: 0.5s ease all;
}

.navegacion-principal.sticky {
  padding: 0 5px;
  transition: 0.5s ease all;
}
.navegacion-principal.sticky::before {
  content: "";
  bottom: 0;
  box-shadow: 0 0.2rem 0.3rem #000;
  height: 0.1rem;
  left: 0;
  position: absolute;
  right: 0;
}

.header.sticky {
  position: fixed;
  z-index: 1;
  background: #000;
}
.header.sticky h1 {
  padding: 0 5px;
  transition: 0.5s ease all;
}

